import { useContext, useState, useRef } from "react";
import TaskContext from "../../Store/TaskContext";
import { v4 as uuidv4 } from "uuid";
import {
  StyledAddTaskBar,
  Container,
  TextArea,
  Footer,
  CategoryBtn,
  SubmitBtn,
  CategoryGroup,
} from "./Styles";

import useRWD from "../../CustomHook/useRWD";

const AddTaskBar = (props) => {
  const [categoryActive, setcategoryActive] = useState("Work");
  const [errorAni, setErrorAni] = useState(false);
  const ref = useRef(null);
  const TaskCtx = useContext(TaskContext);
  const AnimationTime = 500;
  const addTaskHandler = () => {
    let task = ref.current.value;

    if (task.trim().length === 0) {
      setErrorAni(true);
      setTimeout(() => {
        setErrorAni(false);
      }, AnimationTime);
      return;
    }
    TaskCtx.addTask({
      id: uuidv4(),
      category: categoryActive,
      task: task,
      created_time: Date.now(),
      finish: false,
    });
    ref.current.value = "";
    return;
  };
  const categoryActiveHandler = (category) => {
    setcategoryActive(category);
  };
  return (
    <StyledAddTaskBar>
      <Container>
        <TextArea
          name="123"
          id=""
          cols="30"
          rows="10"
          placeholder="規劃今天的生活..."
          ref={ref}
        ></TextArea>
        <Footer AnimationTime={500}>
          <CategoryGroup>
            <CategoryBtn
              onClick={categoryActiveHandler.bind(null, "Work")}
              active={categoryActive === "Work"}
            >
              工作
            </CategoryBtn>
            <CategoryBtn
              onClick={categoryActiveHandler.bind(null, "Study")}
              active={categoryActive === "Study"}
            >
              學習
            </CategoryBtn>
            <CategoryBtn
              onClick={categoryActiveHandler.bind(null, "Other")}
              active={categoryActive === "Other"}
            >
              其他
            </CategoryBtn>
          </CategoryGroup>
          <SubmitBtn
            onClick={addTaskHandler}
            active={errorAni}
          >
            輸入
          </SubmitBtn>
        </Footer>
      </Container>
    </StyledAddTaskBar>
  );
};

export default AddTaskBar;
